$font-stack: "Raleway", "Arial", sans-serif;
$primary-color: #bdc3c7;
$footer-background-color: #f6f6f6;
$facebook-color: #3b5998;
$instagram-color: #405de6;

// Basic Setup
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body {
    background-color: #fff;
    color: #555;
    font-family: $font-stack;
    font-weight: 300;
    font-size: 20px;
    text-rendering: optimizeLegibility;
    overflow-x: hidden;
}

.clearfix {
    zoom: 1
}

.clearfix:after {
    content: '.';
    clear: both;
    display: block;
    height: 0;
    visibility: hidden;
}

//Reusable
.row {
    max-width: 1140px;
    margin: 0 auto;
}

section {
    padding: 105px 0px;
    margin-bottom: 200px;
}

//Headings
h1,
h2 {
    font-weight: 100;
    text-transform: uppercase;
    float: left;
}

h1 {
    font-size: 225%;
    font-weight: 100;
    margin-top: 37px;
    letter-spacing: 3px;
    color: #fff;
    a:link,
    a:visited {
        text-decoration: none;
        color: #fff;
    }
}

h2 {
    font-size: 200%;
    border-left: 1px solid $primary-color;
    padding-left: 15px;
}

header {
    background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.5)), url(img/img-1.jpg);
    background-size: cover;
    background-position: center;
    height: 100vh;
    // background-attachment: fixed;
}

//Main Nav
.main-nav {
    margin-top: 54px;
    list-style: none;
    float: right;
    li {
        margin-left: 38px;
        display: inline-block;
        a:link,
        a:visited {
            padding: 8px 0px;
            text-decoration: none;
            text-transform: uppercase;
            font-size: 85%;
            color: #fff;
            transition: color 0.2s;
        }
        a:hover,
        a:active {
            color: $primary-color;
        }
    }
}

.scroll-to-content {
    text-align: center;
    padding-top: 540px;
    a {
        display: inline-block;
        border-radius: 50%;
        background-color: rgba(255, 255, 255, 0.25);
        padding: 5px 14px 2px 14px;
        i {
            font-size: 175%;
            color: #000;
        }
    }
}

//Footer
footer {
    display: block;
    background-color: $footer-background-color;
    padding: 50px;
    p {
        color: #555;
        text-align: center;
        font-size: 65%;
        margin-top: 50px;
    }
}

.social-links {
    text-align: center;
    list-style: none;
    li {
        display: inline-block;
        margin: auto 30px;
        a:link,
        a:visited {
            color: #555;
            text-decoration: none;
            font-size: 225%;
        }
    }
}

.ion-social-facebook,
.ion-social-instagram-outline {
    transition: color 0.2s;
}

.ion-social-facebook:hover {
    color: $facebook-color;
}

.ion-social-instagram-outline:hover {
    color: $instagram-color;
}

//Gear
.gear-tabs {
    list-style: none;
    float: right;
    li {
        margin-left: 5px;
        display: inline-block;
        cursor: pointer;
        background-color: #f6f6f6;
        padding: 25px 30px 25px 30px;
        color: #444;
        text-decoration: none;
        font-size: 55%;
        font-weight: 400;
        text-transform: uppercase;
        transition: background-color 0.2s;
        &:hover,
        &:active {
            background-color: #eee;
        }
    }
}
